:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsla(230, 17%, 14%, 1);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-focus: hsl(229, 29%, 32%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --card-border: hsl(204, 80%, 49%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--dark-bg);
  color: var(--dark-text2);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5625rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 3rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.4375rem;
  margin-bottom: 2.75rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3.5625rem 1fr;
  grid-template-rows: repeat(2, 1fr);
  border: none;
  gap: 0.3rem 0.5rem;
  margin: 0;
  padding: 0;
}
@media (max-width: 39.9375em) {
  .toggle {
    margin-top: 0.75rem;
  }
}
.toggle label {
  font-size: 0.875rem;
  cursor: pointer;
  align-self: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
  align-self: start;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  grid-column: 2/3;
  grid-row: 1/2;
  position: relative;
  height: 1.5rem;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -0.125rem;
  width: 1.25rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  border-radius: 0.75rem;
  height: 100%;
  position: absolute;
  top: 0;
  width: 100%;
  display: block;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.cards {
  display: flex;
  flex-wrap: wrap;
  margin: -0.9375rem;
  flex-direction: column;
}
@media (min-width: 40em) {
  .cards {
    flex-direction: row;
  }
}

.card {
  background: var(--dark-card);
  color: var(--dark-text1);
  text-align: center;
  padding: 2rem 1.9375rem 1.5rem;
  border-radius: 0.3125rem;
  margin: 0.9375rem;
  position: relative;
  width: auto;
  transition: all 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--dark-card-hover);
}
.card:focus {
  background: var(--dark-card-focus);
  transform: scale(1.05);
}
@media (min-width: 40em) {
  .card {
    width: calc(50% - 1.875rem);
  }
}
@media (min-width: 71.875em) {
  .card {
    width: calc(25% - 1.875rem);
  }
}
.card::after {
  content: "";
  height: 0.3125rem;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 0.3125rem 0.3125rem 0 0;
}
.card.facebook::after {
  background: var(--facebook);
}
.card.twitter::after {
  background: var(--twitter);
}
.card.instagram::after {
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
}
.card.youtube::after {
  background: var(--youtube);
}
.card .platform {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.25rem;
  margin-bottom: 1.5625rem;
}
.card .subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card .icon {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 0;
  width: 1.25rem;
  height: 1.25rem;
}
.card .icon.facebook::before {
  content: url("../images/icon-facebook.svg");
}
.card .icon.twitter::before {
  content: url("../images/icon-twitter.svg");
}
.card .icon.instagram::before {
  content: url("../images/icon-instagram.svg");
}
.card .icon.youtube::before {
  content: url("../images/icon-youtube.svg");
}
.card .username {
  font-weight: 700;
  font-size: 0.75rem;
  margin-left: 0.5625rem;
  letter-spacing: 1px;
}
.card .followers {
  margin-bottom: 1.5625rem;
}
.card .count {
  color: var(--light-bg);
  font-weight: 700;
  line-height: 1;
}
.card .count.small {
  font-size: 2rem;
  line-height: 0.9;
  margin-top: 0.4375rem;
  letter-spacing: 0;
}
.card .count.big {
  letter-spacing: -0.125rem;
  margin-bottom: 0.25rem;
  font-size: 3.5rem;
}
.card .label {
  color: var(--dark-text1);
  font-size: 0.75rem;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.3125rem;
}
.card .change {
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card .change.up {
  color: var(--limegreen);
}
.card .change.up:before {
  content: url("../images/icon-up.svg");
}
.card .change.down {
  color: var(--brightred);
}
.card .change.down:before {
  content: url("../images/icon-down.svg");
}
.card .change.down:before, .card .change.up:before {
  font-size: 0;
  margin-right: 0.25rem;
}

.card-grid {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 1.6875rem 1.9375rem 1.5rem 1.5625rem;
  gap: 1.1875rem;
  margin-bottom: 0.625rem;
}
.card-grid > div {
  flex: 1 1 calc(50% - 1.1875rem);
  text-align: left;
}
.card-grid > div:nth-child(even) {
  text-align: right;
}
.card-grid > div:nth-child(1), .card-grid > div:nth-child(2) {
  align-self: flex-start;
}
.card-grid > div:nth-child(3), .card-grid > div:nth-child(4) {
  align-self: flex-end;
}
.card-grid .change {
  justify-content: flex-end;
}